
html {
    background-color: #334D5C;
    height: 100%;
    margin: 0;
    padding: 0;
}
body {
    background-color: #334D5C;
    margin: 0;
    padding: 0;
}
.page-title {
    text-align: center;
    color: white;
    padding-top: 10px;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    height: 100%;
    background-color: #334D5C;
}
input {
    text-align: center;
    margin-bottom: 10px;
    font-size: 1.3rem;
    border: 0;
    border-radius: 3px;
    background: #fff;
}

/* N A V B A R */
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #45B29D;
}
li {
    float: left;
}
li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-weight: 700;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #111;
}
.active {
    background-color: #334D5C;
}
#icon {
    height: 20px;
    width: 20px;
}
#icon img {
    position: absolute;
    left: 10px;
    top: 10px;
}




